.banner {
  height: 35vmax;
  width: 100%;
  overflow: hidden;
  position: relative;
}

.banner img {
  max-width: 100%;
  max-height: 100%;
}

.bg-gradient {
  position: absolute;
  background-image: linear-gradient(rgba(0, 0, 0, 0), #222);
  bottom: 0;
  height: 100%;
  width: 100%;
}

.banner-content {
  position: absolute;
  top: 50%;
  padding: 2em;
  transform: translateY(-50%);
}

.banner-title {
  margin: 0;
  color: white;
  font-size: 7vmin;
  word-wrap: break-word;
  max-height: 50%;
}
